@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800;900&display=swap");
* {
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  line-height: 150%;
}
body {
  background: #ffffff;
  font-family: "Montserrat", sans-serif;
}
//Main title in Heading section
h1 {
  font-size: 31px;
  font-weight: 700;
}
//Title of the BigCta section
h2 {
  font-weight: 400;
  font-size: 32px;
}
//Usp titles
h3 {
  font-weight: 400;
  font-size: 26px;
}
//Logo label
h5 {
  font-size: 21px;
  font-weight: 900;
}
//Footer text
h6 {
  font-size: 16px;
}
p {
  font-size: 18px;
}
.setPadding {
  padding: 40px 2%;
}
//Drop-shadow to ellemnts
.drop-shadow {
  // -webkit-box-shadow: 10px 10px 19px -6px rgba(168, 168, 168, 1);
  // -moz-box-shadow: 10px 10px 19px -6px rgba(168, 168, 168, 1);
  // box-shadow: 10px 10px 19px -6px rgba(168, 168, 168, 1);
  box-shadow: rgb(149 157 165 / 30%) 0 8px 24px;
}
//Drop-shadow for images
// .drop-shadow-img {
//   filter: drop-shadow(5px 4px 3px rgb(114, 159, 197));
// }

input,
button {
  font-size: 16px;
  outline: none;
  border: none;
  border-radius: 2px;
  padding: 10px 20px;
  font-family: "Montserrat", sans-serif;
}
button {
  cursor: pointer;
  // background-image: linear-gradient(102.1deg, #188a8d 8.7%, #54b778 88.1%);
  background: #5fdf4e;
  color: white;
  font-size: 16px;
  font-weight: 500;
  width: 140px;

  transition: width 0.3s;
}
button:hover {
  width: 165px;
}
input {
  width: 60%;
  max-width: 380px;
  background: #ebebeb;
  color: #2f2f2f;
  font-weight: 500;
  padding-right: 35px;
  ::placeholder {
    color: #2f2f2f;
  }
}
//set the input and the button to be in a row
.input {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  button {
    margin-left: -30px;
  }
}
//Edit scrollbar
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #dee4eb;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #b5bcc2;
  border-radius: 3px;
}

@media screen and (min-width: 700px) {
  //Main title in Heading section
  h1 {
    font-size: 42px;
    font-weight: 700;
  }
  //Title of the BigCta section
  h2 {
    font-weight: 400;
    font-size: 32px;
  }
  //Usp titles
  h3 {
    font-weight: 400;
    font-size: 32px;
  }
  //Logo label
  h5 {
    font-size: 24px;
    font-weight: 700;
  }
  //Footer text
  h6 {
    font-size: 16px;
  }
  p {
    font-size: 18px;
  }
  .setPadding {
    padding: 60px 4%;
  }
}
